import { Scene } from 'babylonjs';
import { MetalitixLoggerOptions } from '../types';
import MetalitixLoggerBase from './mtx-poll-base';

export default class MetalitixLogger extends MetalitixLoggerBase<Scene> {
  constructor(appKey: string, options: MetalitixLoggerOptions = {}) {
    super(appKey, options);
  }

  protected getPositionData = (scene: Scene | null) => {
    const camera = scene?.activeCamera ?? null;
    if (camera === null) {
      return undefined;
    }

    return {
      position: { x: camera.position.x, y: camera.position.y, z: camera.position.z },
      direction: { x: camera.absoluteRotation.x, y: camera.absoluteRotation.y, z: camera.absoluteRotation.z },
    };
  };

  protected getCameraData = (scene: Scene | null) => {
    const camera = scene?.activeCamera ?? null;
    if (camera === null) {
      return undefined;
    }

    return {
      fieldOfView: camera.fov,
      aspectRatio: camera.viewport.width / camera.viewport.height,
      zNearPlane: camera.minZ,
      zFarPlane: camera.maxZ,
    };
  };
}
